// 必须有vue组件，在下载cli时自带
import Vue from 'vue'
// 引入路由模块，自带
import Router from 'vue-router'



// 首页
const First = resolve =>{
	import("./components/first/first.vue").then(module=>{
		resolve(module)
	})
}
// 注册页面
const Register = resolve =>{
	import("./components/login/register").then(module=>{
		resolve(module)
	})
}
// 登录页面
const Login = resolve =>{
	import("./components/login/login").then(module=>{
		resolve(module)
	})
}
// 个人中心页
const My = resolve =>{
	import("./components/login/my").then(module=>{
		resolve(module)
	})
}
// 发现页
const Faxian = resolve =>{
	import("./components/faxian/faxian").then(module=>{
		resolve(module)
	})
}
// 搜索页
const Search = resolve =>{
	import("./components/search/search").then(module=>{
		resolve(module)
	})
}
// import musicbar from './components/musicbar.vue'
// player播放页
const Music = resolve =>{
	import("./components/music").then(module=>{
		resolve(module)
	})
}
//播放子页；
const Player = resolve =>{
	import('./components/bar/player').then(module=>{
		resolve(module)
	})
}

//歌手列表；
const Singer = resolve =>{
	import('./components/list/singer').then(module=>{
		resolve(module)
	})
}

//歌手列表；
const Singers = resolve =>{
	import('./components/list/singers').then(module=>{
		resolve(module)
	})
}
import not from './components/not'
import list from './components/list/components/list'

// 这就是一个异步组件，使用了路由懒加载技术,()中注释的部分是用来打包时，webpack会自动将命名相同的组件放在同个异步块chunk中
// const Search = () => import(/* webpackChunkName: "group-foo" */"./components/search/search.vue")
// 挂载路由器到vue对象下
Vue.use(Router)
export default new Router({
	routes: [
	{ path:'/', redirect:'/first'},
	{ path:'/first', component:First},
	// { path:'/musicbar', component:musicbar},
	{ path:'/music',component:Music},
	{ path:'/player/:id',component:Player},
	{ path:'/faxian', component:Faxian},
	{ path:'/my', component:My },
	{ path:'/singer', component:Singer },
	{ path:'/singers', component:Singers },
	{
		 path:'/search', 
		 component:Search, 
		//  children:[{
		// 	 path:'/search/:id',
		// 	 component:这是要传参过去的页面路径，然后不要忘了上面引入
		//  }]
	},
	{ path:'/register', component:Register },
	{ path:'/login', component:Login},
	{ path:'/list', component:list},
	{ path:'**', component:not},
	]
})