import Vue from 'vue'
import Vuex from 'vuex'
// 2.将vuex注册到vue中
Vue.use(Vuex)
// 3创建store对象
// Action这是异步的对象，当mutaions中的某个方法需要异步执行时，就可以将函数调用放在此内
// mapState 辅助函数 这个是防止计算属性中过多需要计算值，就要使用这个
export default new Vuex.Store({
  // 共享数据的容器
  state:{
    n:sessionStorage.getItem("n")||0,
    id:localStorage.getItem("id"),
    histroy:[]
  },
  // 操作共享数据的方法
  mutations:{
    addn(state){
      state.n++;
      sessionStorage.setItem("n",state.n);
    },
    histroy(state,id,song){
      state.histroy.push({id,song});
    },
    clearhistroy(state){
      state.histroy=[];
    },
  ajjn(state){
    if(state.n==0){return;}
    state.n--;
    sessionStorage.setItem("n",state.n);
  }
  },

  // 获取共享数据的方法
  getters:{
    opn:function(state){
      return state.n;
    },
    getid:function(state){
      return state.id;
    },
    getter:function(state){
      return state.histroy;
    }
  }
})